<template>
  <div class="custom-new-bill">
    <div class="input-group">
      <span class="input-group-text">First name</span>
      <input type="text" aria-label="First name" class="form-control shadow-none" v-model="first_name">
    </div>
    <div class="input-group">
      <span class="input-group-text">Last name</span>
      <input type="text" aria-label="Last name" class="form-control shadow-none" v-model="last_name">
    </div>
    <div class="input-group">
      <input type="text" class="form-control shadow-none" aria-label="Euro amount (with dot and two decimal places)" placeholder="Amount" @change="validateAmount($event)" v-model="amount">
      <span class="input-group-text">{{amount}}</span>
      <span class="input-group-text">€</span>
    </div>
    <div class="input-group mb-3">
      <input type="text" class="form-control shadow-none" placeholder="Description" aria-label="Description" aria-describedby="button-addon2" v-model="description">
      <button class="btn btn-primary shadow-none" type="button" id="button-addon2" @click="putNewBillToDatabase">Add New Bill</button>
    </div>
  </div>
  <navbar-vue></navbar-vue>
</template>
<script>
import NavbarVue from '@/components/Navbar.vue'
import '@/assets/styles/NewBill.css'
import BillHandler from '@/assets/js/BillHandler'
export default {
  name: 'AppNewBill',
  components: {
    NavbarVue
  },
  data () {
    return {
      first_name: '',
      last_name: '',
      amount: '',
      description: ''
    }
  },
  methods: {
    putNewBillToDatabase: async function () {
      await BillHandler.initDB()
      BillHandler.putNewBillToDatabase(this.first_name, this.last_name, this.amount, this.description, false)
      this.first_name = ''
      this.last_name = ''
      this.amount = ''
      this.description = ''
    },
    validateAmount: function (event) {
      if (isNaN(event.target.value)) {
        BillHandler.setError('String values are not allowed as amount', 'top-right')
        this.amount = ''
      }
    }
  }
}
</script>
