<template>
  <div class="custom-app-overview">
    <div class="custom-unpaid-bills-description">
      <button type="button" class="btn btn-primary shadow-none custom-paid-bills-visible-button" @click="setUnpaidBillsVisible()"><i :class="unpaidBillsCollapseIcon"></i> <h3 class="custom-bold">Unpaid Bills</h3></button>
    </div>
    <div class="custom-total-amount border rounded" v-show="unpaidBillsVisible">Total <p class="custom-total-amount-value">{{totalAmountUnpaidBills}}€</p></div>
    <div class="custom-unpaid-bills" v-show="unpaidBillsVisible">
      <div v-html="unpaidBillsHtml" @click="handleClickEvent($event)"></div>
    </div>

    <div class="custom-paid-bills-description">
      <button type="button" class="btn btn-primary shadow-none custom-paid-bills-visible-button" @click="setPaidBillsVisible()"><i :class="paidBillsCollapseIcon"></i> <h3 class="custom-bold">Paid Bills</h3></button>
    </div>
    <div class="custom-total-amount border rounded" v-show="paidBillsVisible">Total <p class="custom-total-amount-value">{{totalAmountPaidBills}}€</p></div>
    <div class="custom-paid-bills" v-show="paidBillsVisible">
      <div v-html="paidBillsHtml" @click="handleClickEvent($event)"></div>
    </div>
  </div>
  <NavbarVue></NavbarVue>
</template>

<script>
import '@/assets/styles/AppOverview.css'
import NavbarVue from './Navbar.vue'
import BillHandler from '@/assets/js/BillHandler'
export default {
  name: 'AppOverview',
  components: {
    NavbarVue
  },
  data () {
    return {
      unpaidBillsHtml: '',
      paidBillsHtml: '',
      totalAmountUnpaidBills: 0,
      totalAmountPaidBills: 0,
      unpaidBillsVisible: true,
      unpaidBillsCollapseIcon: '',
      paidBillsVisible: false,
      paidBillsCollapseIcon: 'fa-solid fa-caret-right'
    }
  },
  methods: {
    getAllBillsFromDatabase: async function () {
      await BillHandler.initDB()
      BillHandler.getAllBillsFromDatabase().then(bills => {
        if (bills[0] !== '') {
          this.unpaidBillsHtml = bills[0]
        } else {
          this.unpaidBillsHtml = '<p>There are no unpaid bills</p>'
        }
        if (bills[1] !== '') {
          this.paidBillsHtml = bills[1]
        } else {
          this.paidBillsHtml = '<p>There are no paid bills</p>'
        }
        this.totalAmountUnpaidBills = bills[2]
        this.totalAmountPaidBills = bills[3]
      }).catch(error => {
        console.log(error.message)
      })
    },
    setBillPaid: async function (key, newValue) {
      BillHandler.setBillPaid(key, newValue)
      this.getAllBillsFromDatabase()
    },
    deleteBill: async function (key) {
      BillHandler.deleteBill(key)
      this.getAllBillsFromDatabase()
    },
    handleClickEvent: function (event) {
      if (event.target.getAttribute('data-customid') === 'set-bill-paid-button-true') {
        this.setBillPaid(event.target.getAttribute('data-billid'), true)
      }
      if (event.target.getAttribute('data-customid') === 'set-bill-paid-button-false') {
        this.setBillPaid(event.target.getAttribute('data-billid'), false)
      }
      if (event.target.getAttribute('data-customid') === 'delete-bill-button') {
        this.deleteBill(event.target.getAttribute('data-billid'))
      }
      if (event.target.getAttribute('data-customid') === 'add-amount-to-bill-button') {
        this.editBill(event.target.getAttribute('data-billid'))
      }
    },
    setPaidBillsVisible: function () {
      if (this.paidBillsVisible === true) {
        this.paidBillsVisible = false
        this.paidBillsCollapseIcon = 'fa-solid fa-caret-right'
      } else {
        this.paidBillsVisible = true
        this.paidBillsCollapseIcon = ''
      }
    },
    setUnpaidBillsVisible: function () {
      if (this.unpaidBillsVisible === true) {
        this.unpaidBillsVisible = false
        this.unpaidBillsCollapseIcon = 'fa-solid fa-caret-right'
      } else {
        this.unpaidBillsVisible = true
        this.unpaidBillsCollapseIcon = ''
      }
    },
    editBill: async function (key) {
      await BillHandler.initDB()
      localStorage.setItem('currentBillId', key)
      this.$router.push('/EditBill')
    }
  },
  async created () {
    this.getAllBillsFromDatabase()
  }
}
</script>
