import { v4 as uuidv4 } from 'uuid'
import { useToast } from 'vue-toastification'

let db
let lastColor = ''

async function initDB () {
  return new Promise((resolve, reject) => {
    const request = window.indexedDB.open('billdb')

    request.onerror = e => {
      console.log('Error opening db', e)
      reject(new Error('Error'))
    }

    request.onsuccess = e => {
      resolve(e.target.result)
      db = e.target.result
    }

    request.onupgradeneeded = e => {
      console.log('onupgradeneeded')
      const db = e.target.result
      const store = db.createObjectStore('bills', { keyPath: 'id' })
      store.createIndex('by_id', 'id')
      store.createIndex('by_firstname', 'first_name')
      store.createIndex('by_lastname', 'last_name')
      store.createIndex('by_amount', 'amount')
      store.createIndex('by_description', 'description')
      store.createIndex('by_paid', 'paid')
      store.createIndex('by_isNewBill', 'isNewBill')
      store.createIndex('by_lastDateUpdated', 'lastDateUpdated')
    }
  })
}

async function putNewBillToDatabase (firstName, lastName, amount, description, paid) {
  firstName = firstName.replace(/\s/g, '')
  lastName = lastName.replace(/\s/g, '')
  amount = amount.replace(/\s/g, '')

  var currentdate = new Date()
  var lastDateUpdated = currentdate.getDate() + '/' + (currentdate.getMonth() + 1) + '/' + currentdate.getFullYear() + ' @ ' + currentdate.getHours() + ':' + currentdate.getMinutes() + ':' + currentdate.getSeconds()

  if (firstName !== '' && lastName !== '' && amount !== '' && description !== '') {
    const store = db.transaction('bills', 'readwrite').objectStore('bills')

    var uuid = generateUUID()

    store.put({ id: uuid.toString(), first_name: firstName, last_name: lastName, amount: amount, description: description, paid: paid, isNewBill: true, lastDateUpdated })
  } else {
    setError('You have to fill in a First Name, Last Name, Amount and Description', 'top-right')
  }
}

async function getAllBillsFromDatabase () {
  return new Promise((resolve, reject) => {
    const store = db.transaction('bills', 'readonly').objectStore('bills')
    let unpaidBillsHtml = ''
    let paidBillsHtml = ''
    let totalAmountUnpaidBills = 0.00
    let totalAmountPaidBills = 0.00
    var bills = []
    // const index = store.index('by_paid')
    const allBills = store.index('by_lastDateUpdated').openCursor(IDBKeyRange.lowerBound(''), 'prev')
    allBills.onsuccess = function (event) {
      const cursor = event.target.result
      if (cursor) {
        if (cursor.value.paid === false) {
          unpaidBillsHtml += '<div class="border rounded custom-form-margin" style="border-color:' + getRandomColor() + '!important"> <div class="custom-form-padding"> <div class="input-group"> <label class="form-label name">' + cursor.value.first_name + '</label> <label class="form-label name">' + cursor.value.last_name + '</label> <label class="form-label">owes you</label> <label class="form-label amount">' + cursor.value.amount + '€' + '</label> <span class="input-group-text" id="basic-addon2"> <button class="btn btn-outline-primary shadow-none" style="--bs-btn-padding-y: .25rem; --bs-btn-padding-x: .5rem; --bs-btn-font-size: 15px;" type="button" id="button-addon2" data-customid="add-amount-to-bill-button" data-billid="' + cursor.value.id + '"><i class="fa-solid fa-plus" data-customid="add-amount-to-bill-button" data-billid="' + cursor.value.id + '"></i></button> <button class="btn btn-outline-success shadow-none" style="--bs-btn-padding-y: .25rem; --bs-btn-padding-x: .5rem; --bs-btn-font-size: 15px;" type="button" id="button-addon2" data-customid="set-bill-paid-button-true" data-billid="' + cursor.value.id + '"><i class="fa-solid fa-check" data-customid="set-bill-paid-button-true" data-billid="' + cursor.value.id + '"></i></button> <button class="btn btn-outline-danger shadow-none" style="--bs-btn-padding-y: .25rem; --bs-btn-padding-x: .5rem; --bs-btn-font-size: 15px;" type="button" id="button-addon2" data-customid="delete-bill-button" data-billid="' + cursor.value.id + '"><i class="fa-solid fa-trash-can" data-customid="delete-bill-button" data-billid="' + cursor.value.id + '"></i></button> </span> <div class="input-group"> <label class="form-label description">' + cursor.value.description + '</label> </div> </div> </div> </div>'
          totalAmountUnpaidBills += parseFloat(cursor.value.amount)
        } else {
          paidBillsHtml += '<div class="border rounded custom-form-margin" style="border-color:' + getRandomColor() + '!important"> <div class="custom-form-padding"> <div class="input-group"> <label class="form-label name">' + cursor.value.first_name + '</label> <label class="form-label name">' + cursor.value.last_name + '</label> <label class="form-label">owes you</label> <label class="form-label amount">' + cursor.value.amount + '€' + '</label> <span class="input-group-text" id="basic-addon2"> <button class="btn btn-outline-danger shadow-none" style="--bs-btn-padding-y: .25rem; --bs-btn-padding-x: .5rem; --bs-btn-font-size: 15px;" type="button" id="button-addon2" data-customid="set-bill-paid-button-false" data-billid="' + cursor.value.id + '"><i class="fa-solid fa-xmark" data-customid="set-bill-paid-button-false" data-billid="' + cursor.value.id + '"></i></button> <button class="btn btn-outline-danger shadow-none" style="--bs-btn-padding-y: .25rem; --bs-btn-padding-x: .5rem; --bs-btn-font-size: 15px;" type="button" id="button-addon2" data-customid="delete-bill-button" data-billid="' + cursor.value.id + '"><i class="fa-solid fa-trash-can" data-customid="delete-bill-button" data-billid="' + cursor.value.id + '"></i></button> </span> <div class="input-group"> <label class="form-label description">' + cursor.value.description + '</label> </div> </div> </div> </div>'
          totalAmountPaidBills += parseFloat(cursor.value.amount)
        }
        cursor.continue()
      } else {
        bills.push(unpaidBillsHtml, paidBillsHtml, Math.round((totalAmountUnpaidBills + Number.EPSILON) * 100) / 100, Math.round((totalAmountPaidBills + Number.EPSILON) * 100) / 100)
        resolve(bills)
      }
    }
    allBills.onerror = event => {
      reject(new Error('Error while getting all Bills'))
    }
  })
}
function setBillPaid (key, newValue) {
  const store = db.transaction('bills', 'readwrite').objectStore('bills')

  const billRequest = store.get(key)
  billRequest.onsuccess = () => {
    const bill = billRequest.result
    bill.paid = newValue

    const updateRequest = store.put(bill)

    updateRequest.onsuccess = () => {
      console.log(updateRequest.result)
    }
  }
}

function addAmountToBill (key, amount, description) {
  const store = db.transaction('bills', 'readwrite').objectStore('bills')
  const billRequest = store.get(key)
  let newAmount = 0.00
  billRequest.onsuccess = () => {
    const bill = billRequest.result
    console.log(billRequest.result)

    if (bill.isNewBill === true) {
      bill.description += ' ' + '<p class="custom-bold">' + bill.amount + '€' + '</p>'
      bill.isNewBill = false
    }

    newAmount = parseFloat(bill.amount) + parseFloat(amount)
    bill.amount = newAmount
    bill.description += '<br>' + description + ' ' + '<p class="custom-bold">' + amount + '€' + '</p>'

    var currentdate = new Date()
    bill.lastDateUpdated = currentdate.getDate() + '/' + (currentdate.getMonth() + 1) + '/' + currentdate.getFullYear() + ' @ ' + currentdate.getHours() + ':' + currentdate.getMinutes() + ':' + currentdate.getSeconds()

    const updateRequest = store.put(bill)

    updateRequest.onsuccess = () => {
      console.log(updateRequest.result)
    }
  }
}

async function deleteBill (key) {
  const store = db.transaction('bills', 'readwrite').objectStore('bills')
  const deleteRequest = store.delete(key)

  // report that the data item has been deleted
  deleteRequest.oncomplete = function () {
    console.log('Bill' + key + 'deleted')
  }
}

function generateUUID () {
  return uuidv4()
}

function setError (errorText, position) {
  const toast = useToast()
  toast.error(errorText, {
    timeout: 5000,
    position: position,
    pauseOnFocusLoss: false,
    pauseOnHover: false,
    closeButton: false,
    closeOnClick: true
  })
}

function getRandomColor () {
  const colors = ['#F23557', '#F0D43A', '#22B2DA', '#3B4A6B']
  let nextColor = ''
  while (nextColor === '') {
    const selectedColor = colors[Math.floor(Math.random() * colors.length)]
    if (selectedColor !== lastColor) {
      nextColor = selectedColor
    }
  }
  lastColor = nextColor
  return nextColor.toString()
}

export default {
  initDB,
  putNewBillToDatabase,
  getAllBillsFromDatabase,
  setBillPaid,
  addAmountToBill,
  deleteBill,
  setError
}
